		height: 200px;
		background: #2A2C37;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: absolute;
		z-index: 10;
		opacity: 1;

		.bottom-information {
			width: 1200px;
			height: 144px;
			margin: 0 auto;
			display: flex;
			justify-content: space-between;
			padding: 0 52px;
			box-sizing: border-box;
			
			.bottom-information-right {
			
				.navigation-box{
					margin-top: 30px;
					.navigation-top{
						display: flex;
						align-items: center;
						.navigation-text{
							height: 14px;
							font-size: 14px;
							font-family: SourceHanSansCN-Regular;
							font-weight: 400;
							line-height: 14px;
							color: #999999;
							letter-spacing: 0.28px;
							opacity: 1;
							margin-right: 35px;
							cursor: pointer;
						}
						.navigation-text:hover{
							color: #FFFFFF;
						}
					}
					.navigation-bottom{
						display: flex;
						align-items: center;
						margin-top: 30px;
						margin-left: 24px;
						.service-hotline-box{
							display: flex;
							margin-right: 100px;
							.service-hotline-img{
								width: 40px;
								height: 40px;
							}
							.service-hotline-content{
								display: flex;
								flex-direction: column;
								justify-content: space-between;
								margin-left: 14px;
								.service-hotline-title{
									height: 14px;
									font-size: 14px;
									font-family: SourceHanSansCN-Regular;
									font-weight: 400;
									line-height: 14px;
									color: #999999;
									letter-spacing: 0.28px;
									opacity: 1;
								}
								.service-hotline-number{
									height: 14px;
									font-size: 14px;
									font-family: SourceHanSansCN-Regular;
									font-weight: 400;
									line-height: 14px;
									color: #999999;
									letter-spacing: 0.28px;
									opacity: 1;
								}
							}
						}
					}
				}
			}

			.bottom-information-left {
				margin-top: 21px;
				margin-left: 56px;

				.applets-box {
					display: flex;
					align-items: center;
					.applets-text {
						height: 14px;
						font-size: 14px;
						font-family: SourceHanSansCN-Regular;
						font-weight: 400;
						line-height: 14px;
						color: #999999;
						letter-spacing: 0.28px;
						opacity: 1;
						margin-right: 16px;
					}
					.weChatCodeUrl{
						width: 102px;
						height: 102px;
						background-color: #FFFFFF;
						display: flex;
						align-items: center;
						justify-content: center;
						img {
							width: 90px;
							height: 90px;
						}
					}
				}
			}

		}
		.address-filing{
			width: 100%;
			height: 56px;
			background: #171C28;
			margin: 0 auto;
			display: flex;
			align-items: center;
			justify-content: center;
			opacity: 1;
			.address-filing-text{
				height: 14px;
				font-size: 14px;
				font-family: SourceHanSansCN-Regular;
				font-weight: 400;
				line-height: 14px;
				color: #999999;
				letter-spacing: 0.28px;
				opacity: 1;
				margin-right: 60px;
			}
		}

	}
</style>
